@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200&display=swap);
:root {
  --main-bg-color: #212f3c;
  --color-blue: #0070f3;
  --second-color: #2e4053;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "JetBrains Mono", sans-serif;
}
body{
  
  background-color: #212f3c;
  
  background-color: var(--main-bg-color);
}
.App {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #212f3c;
  background-color: var(--main-bg-color);
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.msg-error {
  display: none;

  align-items: center;
  justify-content: center;
  text-align: center;

  position: absolute;

  width: 450px;
  height: 40px;
  top: 220px;
  bottom: 310px;
  color: #fff;
  background: #c0392b;
  border-radius: 7px;
}
.msg-error i {
  background-color: transparent;
  margin-right: 15px;
}
.msg-error::before {
  content: "";

  top: -16px;
  position: absolute;
  border-left: 17px solid transparent;
  border-right: 17px solid transparent;

  border-bottom: 17px solid #c0392b;
}
.cont-head h1 {
  margin-top: 10px;
  color: #fff;
}
.cont-head i {
  color: #0070f3;
  color: var(--color-blue);
  font-size: 70px;
}
.cont-search {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 7px;
  margin: 30px;
  width: 97%;
  height: 40px;
  background-color: #2e4053;
  background-color: var(--second-color);
}
.cont-search button {
  border: none;
}
.icon-search {
  color: #0070f3;
  color: var(--color-blue);
  cursor: pointer;
  width: 33px;
  height: 33px;
  background-color: transparent;
}
.icon-search i {
  background-color: transparent;
}
.button-search {
  background-color: #0070f3;
  background-color: var(--color-blue);
  cursor: pointer;
  color: #fff;
  width: 70px;
  height: 30px;
  border-radius: 7px;
}

.input-search {
  font-size: 17px;
  color: #fff;
  text-align: center;
  outline: none;
  border: none;
  width: 280px;
  height: 40px;
  background-color: #2e4053;
  background-color: var(--second-color);
}

.cont-user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.user {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.user-name {
  font-size: 30px;
}
.user-login {
  color: var(--color-blue);
}
.user-loc {
  margin-top: 10px;
  display: flex;
  grid-gap: 55px;
  gap: 55px;
}

.user-loc .fas {
  color: var(--color-blue);
  margin-right: 10px;
}
.img-profile {
  border-radius: 100%;
  border: 5px solid var(--color-blue);
}
.user-fav {
  margin-top: 20px;
  background-color: var(--second-color);
  display: flex;

  text-align: center;
  justify-content: space-evenly;
  align-items: center;
  width: 400px;
  height: 50px;
  border-radius: 7px;
}

.cont-repos {
  background-color: var(--second-color);
  margin-top: 20px;
  width: 90%;
  display: flex;
  margin-bottom: 40px;
  flex-wrap: wrap;
  border-radius: 7px;
}
.repo {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  grid-gap: 30px;
  gap: 30px;
  border-radius: 7px;
  padding: 30px;
  margin: 20px auto;
  width: 300px;
  height: 130px;
  background-color: var(--main-bg-color);
}
.repo-title {
  color: #fff;
  display: flex;
  justify-content: start;
  align-items: center;
  width: 100%;
}
.repo-title h1 {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: #fff;
  background-color: transparent;
  font-size: 15px;
}
.repoicon{
  margin-right: 13px;
}
.public{
  padding: 4px;
  border-radius: 30px;
  border: 1px solid #fff;
  margin-left: auto;
}
.repo-info {
  color: #fff;
  display: flex;
  justify-content: space-between;
}
.repo-info .size {
  font-size: 14px;
}
.info-t {
  font-size: 12px;
  display: flex;
}
.info-type {
  margin-right: 10px;
}

